@import url(https://fonts.googleapis.com/css2?family=Lora&family=Open+Sans:wght@300&family=Ubuntu:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora&family=Montserrat:wght@800&family=Open+Sans:wght@300&family=Ubuntu:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
background: url(https://www.gannett-cdn.com/presto/2020/02/12/USAT/89057f46-af66-4156-a977-483bc83fc0ba-Mazda3-20-1.jpg?crop=2999,1687,x0,y152&width=1600&height=800&format=pjpg&auto=webp) no-repeat center center fixed;
background-size: cover;
width: 100%;
height: 100%;
}
.App{
  width: 50rem;
  margin-left: 5rem;
  margin-top: 8rem;
}
.App p{
  font-family: 'Ubuntu', sans-serif;
  color: 'black';

}
.App h1{
  font-family: 'Lora', serif;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Ubuntu', sans-serif;
  line-height: 3rem;
}
@media(max-width:768px){
.App {width: 75%}
}


    

